import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state = {
	addBook:[],
	sex:[]
}

const mutations = {
	
	addBook(state, res) {
		state.addBook.push(res)
	},
	addCancel(state, res){
		state.addBook.splice(res,1)
	},
	sexButton(state, res){
		
		state.sex.length = 1
		state.sex.push(res)
		state.sex.shift(res)
	}
}


const store = new Vuex.Store({
	state,
	mutations
})

export default store