import Vue from 'vue'
import Router from 'vue-router'
import axios from 'axios'
// import ListWarper from '@/components/ListWarper.vue'
// import Select from '@/views/Select.vue'
// import Cation from '@/views/Cation.vue'
// import Vip from '@/views/Vip.vue'
// import Rank from '@/views/Rank.vue'
// import LoginPage from '@/views/LoginPage.vue'
// import SearchBar from '@/views/SearchBar.vue'

Vue.prototype.$http = axios
Vue.use(Router)

export default new Router({
  routes: [
	{
		path: '/',
		name: 'select',
		component: () => import( './views/Select.vue')
	},
	{
		path: '/cation/',
		name: 'cation',
		component: () => import( './views/Cation.vue'),
	},
	{
		path: '/rank/',
		name: 'rank',
		component: () => import( './views/Rank.vue')
	},
	{
		path: '/vip/',
		name: 'vip',
		component: () => import('./views/Vip.vue')
	},
	{
		path:'/login/',
		name:'login',
		component: () => import('./views/LoginPage.vue')
	},
	{
		path:'/search/',
		name:'search',
		component: () => import('./views/SearchBar.vue')
	},
	
	{
		path:'/listwarper/:id/:name',
		name:'listwarper',
		component: () => import('./components/ListWarper.vue')
	},
	{
		path:'/bookdeta/:name',
		name:'bookdeta',
		component: () => import('./components/BookDeta.vue')
	},
	{
		path:'/bookshelf/',
		name:'bookshelf',
		component: () => import('./components/BookShelf.vue')
	},
	{
		path:'/ranklist/:name/:id',
		name:'ranklist',
		component: () => import('./components/RankList.vue')
	},
	{
		path:'/bookchapter/:id/:title',
		name:'bookchapter',
		component: () => import('./components/BookChapter.vue'),
	},
	{
		path:'/reading/:id/:link',
		name:'reading',
		component: () => import('./components/Read.vue'),
	},
	
	
  ],
	scrollBehavior (to, from, savedPosition) {
		return { x: 0, y: 0 }
	}
})
